import { FC } from 'react';

import { Wrapper } from './Tag.styles';
import { TagProps } from './Tag.types';

const Tag: FC<TagProps> = ({
  children,
  size = 'base',
  isExpanded = false,
  ...rest
}) => {
  return (
    <Wrapper {...rest} $size={size} $variant={isExpanded ? 'expanded' : 'base'}>
      {children}
    </Wrapper>
  );
};

export default Tag;
